<template>
  <div>
    <div v-if="loading">
      <b-progress :value="loading? 50 : 100" :max="100" animated></b-progress>
    </div>
    <b-row v-if="!loading" class="animated fadeIn">
      <b-col :lg="12">
        <b-button class="mb-2 mr-2" @click="refresh">Refresh</b-button>
      </b-col>

      <b-col :lg="current ? 6 : 12">
        <c-table
          :table-data="items"
          :fields="fields"
          hover
          caption="Users"
          :rowClicked="rowClicked"
        ></c-table>
      </b-col>

      <b-col lg="6" v-if="current" class="animated fadeIn">
        <b-card class="card-accent-info" :header="'Detail of :' + current.UserName + ''">
          <b-form>
            <b-form-group v-for="field in fields" :key="field.key">
              <b-input-group
                v-if="(['ID', 'status'].indexOf(field.key) === -1) && field.key != 'LockoutEnabled'"
              >
                <b-input-group-prepend>
                  <b-input-group-text>{{field.key}}</b-input-group-text>
                </b-input-group-prepend>
                <b-form-input disabled  type="text" v-model="current[field.key]"></b-form-input>
              </b-input-group>
            </b-form-group>
            <b-input-group style="margin-bottom:3%">
              <b-input-group-prepend>
                <b-input-group-text >Phone number</b-input-group-text>
              </b-input-group-prepend>
              <b-form-input disabled type="text" v-model="current.PhoneNumber"></b-form-input>
            </b-input-group>
            <b-input-group style="margin-bottom:3%">
              <b-input-group-prepend>
                <b-input-group-text>Point</b-input-group-text>
              </b-input-group-prepend>
              <b-form-input type="text" disabled v-model="current.Point"></b-form-input>
            </b-input-group>
            <div
              class="btn-group form-actions animated fadeIn"
              v-if="!detailMessage && current.Id !== 0 && !current.LockoutEnabled"
            >
              <b-button @click="lock" variant="outline-danger" >Lock account</b-button>
            </div>
            <div
              class="btn-group form-actions animated fadeIn"
              v-if="!detailMessage && current.Id !== 0 && current.LockoutEnabled"
            >
              <b-button @click="unlock" variant="outline-primary" >Unlock account</b-button>
            </div>
            <div v-if="detailMessage">
              <b-button @click="hideWarning" block variant="outline-warning">{{detailMessage}}</b-button>
            </div>
          </b-form>
        </b-card>
      </b-col>
    </b-row>
  </div>
</template>

<script>
import cTable from "@/views/base/Table.vue";
import { Switch as cSwitch } from "@coreui/vue";
import { openapi, methods, routes } from "@/openapi";

export default {
  name: "tables",
  components: { cTable, cSwitch },
  data: () => {
    return {
      loading: true,
      items: [],
      current: null,
      detailMessage: "",
      fields: [
        { key: "UserName", sortable: true },
        { key: "Fullname", sortable: true },
        { key: "Email", sortable: true },
        { key: "Address", sortable: true },
        { key: "LockoutEnabled", sortable: true, label: "Locked" }
      ]
    };
  },
  mounted: function() {
    this.refresh();
  },
  methods: {
    refresh: function() {
      this.loading = true;
      this.current = null;
      openapi(methods.GET, routes.USERS).then(data => {
        this.loading = false;
        this.items = data;
      });
    },
    rowClicked: function(user, i) {
      this.current = user;
    },
    hideSuccess: function() {
      setTimeout(() => {
        this.detailMessage = "";
      }, 1000);
    },
    hideWarning: function() {
      this.detailMessage = "";
    },
    lock: function() {
      this.detailMessage = "Locking...";
      openapi(methods.POST, routes.LOCKACCOUNT, this.current, {
        userId: this.current.Id
      }).then(data => {
        this.detailMessage = "Lock successfully!";
        this.current.LockoutEnabled = true;
        this.hideSuccess();
      });
    },
    unlock: function() {
      this.detailMessage = "Unlocking...";
      openapi(methods.POST, routes.UNLOCKACCOUNT, this.current, {
        userId: this.current.Id
      }).then(data => {
        this.detailMessage = "Unlock successfully!";
        this.current.LockoutEnabled = false;
        this.hideSuccess();
      });
    }

  }
};
</script>